.SideDrawer{
    position: fixed;
    width: 280px;
    max-width: 70%;
    height :100%;
    background-color : white;
    padding: 32px 16px;
    top :0;
    left:0;
    z-index: 200;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

.Open{
    transform: translateX(0)
}

.Close{
    transform: translateX(-100%)
}

.Logo{
    height : 11%;
    margin-bottom: 16px;
}

@media (min-width : 500px) {
    .SideDrawer{
        display: none;
    }
}